../assets/images/ic-player-frame-jump.ttf?rxg7gj\") format('truetype');\n  font-weight: normal;\n  font-style: normal;\n}\n@font-face {\n  font-family: 'op-icons';\n  src: url('../assets/fonts/fontello.ttf?13010392') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n}\n@font-face {\n  font-family: 'op-seek-icons';\n  src: url('../assets/fonts/seek-icons.ttf?13010392') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n}\n\n/**\n * @brief   common style\n * */\n\n:root {\n  --op-accent-color: #50e3c2;\n}\n\n.op-wrapper.ovenplayer {\n  position: relative;\n  max-height: 100%;\n\n  overflow: hidden;\n  zoom: 1 !important;\n  width: 100%;\n  display: block;\n  font-family: Helvetica,Arial,sans-serif;\n  background-color: #000;\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box;\n  color: #fff;\n  font-size: 14px;\n  font-weight: 100;\n  outline: 0;\n\n  -webkit-touch-callout: none;\n  user-select: none;\n\n  * {\n    box-sizing: inherit;\n  }\n\n  object{\n    width:100%; height:100%;\n    position: absolute;\n  }\n\n  &:before, &:after {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n  }\n  & *, & *:before, & *:after {\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n  }\n\n  &.op-fullscreen {\n    width: 100% !important;\n    height: 100% !important;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: 9999;\n    margin: 0;\n    position: fixed;\n\n    .op-watermark-container,\n    .op-thumbnail-container {\n      width: 100vw;\n      max-width: calc(100vh * 16 / 9);\n      height: calc(100vw * 9 / 16);\n      max-height: 100vh;\n      top: 50%;\n      left: 50%;\n      transform: translate(-50%, -50%);\n    }\n\n  }\n  &.op-autohide{\n    cursor: none;\n\n    .op-bottom-panel{\n\n      -webkit-animation-name: op-slideOutDown;\n      animation-name: op-slideOutDown;\n\n      -webkit-animation-duration: 0.2s;\n      animation-duration: 0.2s;\n      -webkit-animation-fill-mode: both;\n      animation-fill-mode: both;\n    }\n    .op-progressbar-container, .op-controls .op-button{\n      cursor: none;\n    }\n    .op-caption-text-container {\n      bottom: 25px;\n    }\n  }\n\n  &.op-no-controls {\n\n    .op-controls-container {\n      display: none !important;\n    }\n  }\n\n  .op-ratio {\n    padding-bottom: 56.25%; /*16:9*/\n  }\n  & ::-webkit-scrollbar {width:5px; height:8px;}\n  & ::-webkit-scrollbar-button:start:decrement, ::-webkit-scrollbar-button:end:increment {display: block; height:3px; background:#2f2f3e;}\n  & ::-webkit-scrollbar-track {background: #2f2f3e; -webkit-border-radius: 10px; border-radius:10px;}\n  & ::-webkit-scrollbar-thumb {height: 50px; width: 50px; background:#606071; -webkit-border-radius: 8px; border-radius: 8px;}\n\n  .op-clear{\n    color: inherit;\n    background-color: transparent;\n    padding: 0;\n    margin: 0;\n    float: none;\n    font-family: Helvetica,Arial,sans-serif;\n    font-size: 1em;\n    line-height: 1em;\n    list-style: none;\n    text-transform: none;\n    vertical-align: baseline;\n    border: 0;\n    font-variant: inherit;\n    font-stretch: inherit;\n    -webkit-tap-highlight-color: rgba(255,255,255,0);\n  }\n}\n\n.op-player {\n  position: absolute;\n  top: 0;\n  height: 100%;\n  width : 100%;\n\n\n\n  .op-core-ui-wrapper {\n    position : relative;\n    height: 100%;\n  }\n\n  .op-media-element-container {\n   /* display: block;\n    height: 100%;\n    width: auto;\n    position : relative;*/\n\n\n    position: absolute;\n    left: 0px;\n    top: 0px;\n    width: 100%;\n    height: 100%;\n\n    video {\n      position: absolute;\n      left: 0;\n      top: 0;\n      width: 100%;\n      height : 100%;\n    }\n    object {\n      width: 100%;\n      height : 100%;\n    }\n  }\n\n  .op-ui {\n    /*position : absolute;\n    bottom : 0px;\n    left : 0px;\n    width: 100%;\n    height: 100%;*/\n    .op-controls-container{}\n    .op-helpers-container{}\n  }\n}\n.op-ads{\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  padding-bottom : 34px;\n  //pointer-events: none;\n  top:0;\n  &>div{\n    position: absolute !important;\n    width: 100% !important;\n    height: 100% !important;\n    iframe{\n      pointer-events :auto;\n    }\n  }\n\n  video.op-ads-vast-video{\n    background-color: rgb(0, 0, 0);\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    left: 0;\n    top: 0;\n    z-index: 1;\n  }\n\n  .op-ads-button{\n    bottom: 36px;\n    cursor: default;\n    margin-right: 4px;\n    pointer-events: auto;\n    position: absolute;\n    right: 0;\n    z-index: 1;\n    width: auto !important;\n    height: auto !important;\n    border-radius: 4px;\n    background-color: rgba(18, 18, 28, 0.7);\n    min-width: 155px;\n    display: none;\n\n    .op-ads-textview{\n      color: #e6e6e6;\n      //font-family: arial,sans-serif;\n      font-weight: normal;\n      font-size: 11px;\n      padding: 6px 12px;\n      text-align: center;\n      display: inline-block;\n      width: 100%;\n      vertical-align: middle;\n    }\n    .videoAdUiAction {\n      padding: 8px 24px;\n      cursor: pointer;\n      direction: ltr;\n      font-weight: normal;\n      font-size: 20px;\n      border : 1px solid rgba(255,255,255,0.5);\n      &:hover{\n        border-radius: 4px;\n        border : 1px solid rgba(255,255,255,1);\n      }\n      i{\n        display: inline-block;\n        width : auto;\n      }\n    }\n  }\n\n}\n\n/**\n * @brief   common components\n * */\n\n.op-button {\n  display: inline-block;\n  border: none;\n  background: transparent;\n  padding: 0;\n  color: inherit;\n  text-align: inherit;\n  overflow: hidden;\n  font-weight: 100;\n  text-indent: 0 !important;\n\n  &:focus, & {\n    outline: 0;\n  }\n}\n\ni.op-con{\n  font-family: \"op-icons\", \"op-seek-icons\";\n  font-style: normal;\n  font-weight: normal;\n  speak: none;\n\n  font-size: inherit;\n  width: auto;\n  font-size : 30px;\n  line-height: 30px;\n\n  display: block;\n  text-decoration: inherit;\n  text-align: center;\n\n  /* For safety - reset parent styles, that can break glyph codes*/\n  font-variant: normal;\n  text-transform: none;\n\n  /* Font smoothing. That was taken from TWBS */\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n\n  cursor: pointer;\n\n  &.disable {\n    color : #4f4f4f;\n  }\n  &.op-close-icon{&::before{content: \"\\e900\";}}\n  &.op-pause-big{&::before{content: \"\\e911\";}}\n  &.op-fullscreen-compress{&::before{content: \"\\e901\";}}\n  &.op-fullscreen-expand{&::before{content: \"\\e902\";}}\n  &.op-arrow-left{&::before{content: \"\\e903\";}}\n  &.op-arrow-right{&::before{content: \"\\e909\";}}\n  &.op-empty-video{&::before{content: \"\\e904\";}}\n  &.op-play{&::before{content: \"\\e906\";}}\n  &.op-replay{&::before{content: \"\\e908\";}}\n  &.op-seek-back{&::before{content: \"\\e920\";}}\n  &.op-seek-forward{&::before{content: \"\\e921\";}}\n  &.op-playlist-icon{&::before{content: \"\\e907\";}}\n  &.op-replay-big{&::before{content: \"\\e908\";}}\n  &.op-setting{&::before{content: \"\\e90A\";}}\n  &.op-pause{&::before{content: \"\\e90C\";}}\n  &.op-volume-small{display:none;margin-top:-1px;&::before{content: \"\\e90D\";}}\n  &.op-volume-mute{display:none;&::before{content: \"\\e90E\";}}\n  &.op-volume-max{&::before{content: \"\\e90F\";}}\n  &.op-play-big{&::before{content: \"\\e910\";}}\n  &.op-warning{&::before{content: \"\\e912\";}}\n}\n.op-badge{\n  display: inline-block;\n  padding: .75em .714em .714em .680em;\n  font-size: 1em;\n  line-height: 1;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: baseline;\n  border-radius: 0.286em;\n  background-color: #12121c;\n}\n.op-playlist {\n  position : absolute;\n  width : 100%;\n  height:100%;\n  left: 0;\n  top : 0;\n  padding: 2.857em;\n  background: #000;\n  z-index : 6;\n  .op-badge{\n    position: absolute;\n    top: 0.857em;\n    right: 0.857em;\n    opacity: 0.7;\n  }\n  .btn-left {\n    float : left;\n    font-size:2.857em;\n  }\n  .btn-right {\n    float : right;\n    font-size: 2.857em;\n  }\n\n  .op-playlist-header{\n    font-size: 2.857em;\n    font-weight: bold;\n    &:after{\n      display: block;\n      content: '';\n      clear: both;\n    }\n    .op-con.op-close-icon{\n      float : right;\n    }\n  }\n  .op-playlist-body{\n    height: 100%;\n    overflow: hidden;\n    position : relative;\n    .op-playlist-body-arrows{\n      margin-top: -15px;\n      position: absolute;\n      top: 50%;\n      height : 30px;\n      -ms-transform: translateY(-50%);\n      transform: translateY(-50%);\n      width: 100%;\n      left: 0;\n    }\n    .op-playlist-body-container{\n      width: 100%;\n      height : 100%;\n      position : relative;\n      margin-right : auto;\n      margin-left : auto;\n      max-width : 992px;\n      .op-playlist-body-center{\n        margin: 0;\n        position: absolute;\n        top: 50%;\n        padding-right : 3em;\n        padding-left : 3em;\n        -ms-transform: translateY(-50%);\n        transform: translateY(-50%);\n        width: 100%;\n      }\n    }\n    .op-playlist-body-row{\n      margin-right: -15px;\n      margin-left: -15px;\n      display: -ms-flexbox;\n      display: flex;\n      -ms-flex-wrap: wrap;\n      flex-wrap: wrap;\n\n\n      .op-playlist-card{\n        padding-right : 15px;\n        padding-left : 15px;\n        margin : 15px 0;\n        -ms-flex-preferred-size: 0;\n        flex-basis: 0;\n        -ms-flex-positive: 1;\n        flex-grow: 1;\n        -ms-flex: 0 0 33.333333%;\n        flex: 0 0 33.333333%;\n        max-width: 33.333333%;\n        cursor: pointer;\n\n        &.active {\n          color: #50e3c2;\n          color : var(--op-accent-color);\n          .op-playlist-card-thumbnail{\n            border-color : #50e3c2;\n            border-color : var(--op-accent-color);\n          }\n        }\n\n        .op-playlist-card-thumbnail{\n          position: relative;\n          display: block;\n          width: 100%;\n          padding: 0;\n          overflow: hidden;\n          border : 0.214em solid transparent;\n          background-color: #000;\n\n          img{\n            position: absolute;\n            top: 0;\n            bottom: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n            border: 0;\n          }\n          &.empty {\n            background-color: #bababa;\n            &>i{\n              margin: 0;\n              position: absolute;\n              top: 50%;\n              left: 50%;\n              transform: translate(-50%, -50%);\n              color : #fff !important;\n              font-size : 1.6em !important;\n            }\n          }\n          &::before {\n            padding-top: 56.25%;\n            display: block;\n            content: \"\";\n          }\n        }\n        .op-playlist-card-title{\n          margin-top : 0.857em;\n          font-size: 1.429em;\n          padding: 2px 0;\n          white-space: nowrap;\n          overflow: hidden;\n          text-overflow: ellipsis;\n        }\n\n      }\n    }\n\n  }\n}\n\n\n/**\n * @brief   Message Box\n * */\n\n.op-message-box {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index : 4;\n  .op-message-container {\n    position: absolute;\n    top: 45%;\n    margin-top : -35px;\n    width: 100%;\n    padding: 0 12px;\n    text-align: center;\n\n    .op-message-text {\n      display: inline-block;\n      background-color: rgba(18,18,28,.3);\n      padding: 0.8em 1.2em;\n      word-wrap: break-word;\n      max-width : 80%;\n      border-radius: 4px;\n      cursor: pointer;\n      .op-message-description{\n        font-size : 12px;\n        margin-top: 12px;\n        color : #999;\n      }\n    }\n\n\n    .op-message-icon{\n      display: inline-block;\n      margin-top : 12px;\n      width: 100%;\n      i.op-con{\n        cursor: pointer;\n        font-size: 80px;\n        width : 80px;\n        height : 80px;\n        line-height : 80px;\n        display: inline-block;\n        text-shadow: 2px 2px 7px rgb(0 0 0 / 80%), 0px 0px 1px rgb(0 0 0);\n      }\n    }\n    /*.op-message-button{\n      display: inline-block;\n      padding : 1em;\n      margin-left : 0.4em;\n      background-color: rgba(28,28,28,.4);\n      cursor : pointer;\n    }*/\n  }\n}\n\n.op-message-box.op-message-box-default-cursor .op-message-container .op-message-text {\n  cursor: default;\n}\n\n.op-message-box.op-message-box-default-cursor .op-message-container .op-message-icon i.op-con {\n  cursor: default;\n}\n\n/**\n * @brief   Big button\n * */\n\n.op-bigbutton-container {\n  cursor: pointer;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n  z-index : 5;\n  .op-bigbutton {\n    width: 80px;\n    height: 80px;\n    display: block;\n    font-size: 80px;\n    line-height: 80px;\n    text-shadow: 2px 2px 7px rgb(0 0 0 / 80%), 0px 0px 1px rgb(0 0 0);\n  }\n}\n\n/**\n * @brief   Thumbnail\n * */\n\n.op-thumbnail-container{\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  //padding-bottom: 56.25%;\n  top: 0;\n  left: 0;\n  //transform: translate(-50%, -50%);\n  z-index : 2;\n  pointer-events: none;\n\n  .op-thumbnail-wrapper{\n    position: absolute;\n    left : 0;\n    top : 0;\n    width: 100%;\n    height : 100%;\n    img{\n      width:100%;\n      height:100%;\n    }\n    .op-thumbnail-header{\n      position: absolute;\n      left : 1rem;\n      top : 1rem;\n      padding: 0 0.5rem;\n      font-size: 2.857em;\n      font-weight: bold;\n      line-height: 1.4;\n      text-shadow: 2px 2px 7px rgb(0 0 0 / 80%), 0px 0px 1px rgb(0 0 0);\n      &:after{\n        display: block;\n        content: '';\n        clear: both;\n      }\n    }\n  }\n}\n\n/**\n * @brief   WaterMark\n * */\n\n.op-watermark-container {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  //padding-bottom: 56.25%;\n  top: 0;\n  left: 0;\n  //transform: translate(-50%, -50%);\n  z-index: 3;\n\n  .op-watermark {\n    position: absolute;\n    display: inline-block;\n\n    img {\n      width: 100%;\n      height: 100%;\n    }\n\n    .op-watermark-text {\n      font-size: 14px;\n    }\n  }\n}\n\n/**\n * @brief   Setting panel\n * */\n\n.op-setting-panel {\n  position: absolute;\n  bottom: 55px;\n  right: 12px;\n  overflow-y : auto;\n  max-height:100%;\n  width: 260px;\n  //font-size: 0.8em;\n  user-select: none;\n  background-color: rgba(28,28,28,.9);\n  text-shadow: 0 0 2px rgba(0,0,0,.5);\n  z-index: 8;\n\n  &.background{\n    //opacity: 0;\n    //background-color: rgba(28,28,28,.0);\n    display: none;\n  }\n  .op-setting-title, .op-setting-item {\n    width: 100%;\n    height: 38px;\n    line-height: 38px;\n    cursor: pointer;\n    outline: none;\n    text-align: left;\n  }\n\n  .op-setting-title-container{\n    .op-setting-title{\n      .op-setting-title-title {\n        padding-left: 12px;\n        font-weight: bold;\n        //font-size: 0.9rem;\n      }\n      .op-setting-title-previcon {\n        padding: 0 0 0 12px;\n        margin-right: -6px;\n      }\n\n    }\n  }\n\n  .op-setting-item-container{\n    .op-setting-item{\n      &:hover {\n        background-color: rgba(255,255,255,.1);\n      }\n\n      .op-setting-item-title {\n        padding-left: 12px;\n      }\n      .op-setting-item-nexticon {\n        float: right;\n        padding-right: 12px;\n        margin-left: -6px;\n      }\n      span.op-setting-item-value {\n        float: right;\n        padding-right: 12px;\n      }\n      .op-setting-item-checked {\n        padding-left: 12px;\n        visibility: hidden;\n      }\n      .op-setting-item-checked.op-show {\n        visibility: visible;\n      }\n    }\n  }\n}\n\n\n\n/**\n * @brief   Controls (container)\n * */\n\n.op-controls-container{\n   display: none;\n  .op-bottom-panel {\n    position: absolute;\n    left: 0px;\n    bottom: 0px;\n    width : 100%;\n    z-index : 5;\n    .op-gradient-bottom {\n      position: absolute;\n      width: 100%;\n      height: 100%;\n      background-color: rgba(18, 18, 18, 0.5);\n      pointer-events: none;\n      /*-moz-transition: opacity .25s cubic-bezier(0.0,0.0,0.2,1);\n      -webkit-transition: opacity .25s cubic-bezier(0.0,0.0,0.2,1);\n      transition: opacity .25s cubic-bezier(0.0,0.0,0.2,1);*/\n    }\n    -webkit-animation-name: op-slideInUp;\n    animation-name: op-slideInUp;\n\n    -webkit-animation-duration: 0.5s;\n    animation-duration: 0.5s;\n    -webkit-animation-fill-mode: both;\n    animation-fill-mode: both;\n\n    .op-progressbar-container {\n      display: block;\n      position: relative;\n      width: 100%;\n      height: 4px;\n      cursor: pointer;\n      bottom: 50px;\n\n      &.op-progressbar-container-disabled {\n        cursor: default;\n      }\n\n      .op-progressbar-padding {\n        position: absolute;\n        width: 100%;\n        height: 15px;\n        bottom: -5px;\n      }\n    }\n\n\n    /*contols container*/\n    .op-controls {\n      position: relative;\n      width: 100%;\n      height: 50px;\n      text-align: left;\n      overflow: hidden;\n\n      &:after{\n        content: '';\n        clear: both;\n      }\n      .op-setting-button {\n        position: relative;\n        margin-right: 12px;\n      }\n      .op-playlist-button {\n        position: relative;\n        margin-right: 12px;\n      }\n\n      .op-navigators{\n        float : left;\n        height: 30px;\n        line-height: 30px;\n      }\n      .op-left-controls {\n        position: absolute;\n        top: 0;\n        left: 0;\n        padding: 14px 0 10px 0;\n        &:after{\n          content: '';\n          clear: both;\n        }\n      }\n      .op-right-controls {\n        position: absolute;\n        top: 0;\n        right: 0;\n        padding: 14px 0 10px 0;\n        &:after{\n          content: '';\n          clear: both;\n        }\n      }\n\n      /*maybe not use*/\n      .op-frame-buttons {\n        position: relative;\n        display: inline-block;\n        margin-left: 14px;\n        overflow: hidden;\n        font-weight: 100;\n        height: 30px;\n\n        .op-frame-button{\n          margin-right: 6px;\n          position: relative;\n          text-align: center;\n          color : #fff;\n          .frame-icon{\n            position: relative;\n\n            &.reverse{\n              &:after {\n                content: '\\e900';\n              }\n            }\n            &:after {\n              font-family: 'frameIcon' !important;\n              speak: none;\n              content: '\\e901';\n              font-style: normal;\n              font-weight: normal;\n              font-variant: normal;\n              text-transform: none;\n\n              /* Better Font Rendering =========== */\n              -webkit-font-smoothing: antialiased;\n              -moz-osx-font-smoothing: grayscale;\n              position: absolute;\n              font-size: 2.4em;\n              left : 0;\n              line-height:30px;\n              height:30px;\n            }\n            .btn-text{\n              font-weight: bold;\n              font-size : 0.8em;\n              line-height:30px;\n              height:30px;\n            }\n\n          }\n\n        }\n\n      }\n    }\n  }\n\n}\n\n\n/**\n * @brief   Progressbar\n * */\n\n.op-progressbar {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  outline: none;\n  margin-top : 10px;\n\n  .op-play-background-color {\n    background-color: #50e3c2; // for ie 11\n    background-color: var(--op-accent-color);\n  }\n\n  .op-progress-list {\n    position: relative;\n    height: 100%;\n    background: rgba(255,255,255,0.2);\n\n    .op-load-progress,\n    .op-play-progress,\n    .op-hover-progress {\n      position: absolute;\n      left: 0;\n      bottom: 0;\n      width: 100%;\n